import { Notify } from 'quasar';

const notify = function(data, isUpload = false) {
  let message;
  if (isUpload) {
    message = data.success ? '' : '';
  } else {
    message = data.message;
  }
  Notify.create({
    type: data.success ? 'positive' : 'negative',
    message: message
  });
};

export default notify;
